.keyboard {
    /* width: 850px; */
    background-color: #000;
    padding: 5px 0;
}

.keyboard ::-moz-selection {
    background: none;
}

.keyboard ::selection {
    background: none;
}

.keyboard-row {
    display: -ms-flexbox;
    display: flex;
}

.keyboard-row * {
    -ms-flex: 1 1;
        flex: 1 1;
}

.keyboard-button {
    padding: 10px;
    font-size: 1.5rem;
    color: white;
    margin: 3px;
    -webkit-box-shadow: 0 1px 2px 0 rgba(60, 64, 67, 0.302),
        0 1px 3px 1px rgba(60, 64, 67, 0.149);
            box-shadow: 0 1px 2px 0 rgba(60, 64, 67, 0.302),
        0 1px 3px 1px rgba(60, 64, 67, 0.149);
}
.keyboard-button.is-lighting {
    color: #2779bd;
    -webkit-box-shadow: 2px 1px 3px 0#2779BD, 2px 1px 3px 2px#2779BD;
            box-shadow: 2px 1px 3px 0#2779BD, 2px 1px 3px 2px#2779BD;
}
.keyboard-button.is-error {
    color: #e3342f;
    -webkit-box-shadow: 2px 1px 3px 0 #e3342f, 2px 1px 3px 2px #e3342f;
            box-shadow: 2px 1px 3px 0 #e3342f, 2px 1px 3px 2px #e3342f;
}
.space-key {
    -ms-flex: 10 1;
        flex: 10 1;
}

.shift-key {
    -ms-flex: 3 1;
        flex: 3 1;
}

.caps-key {
    -ms-flex: 2.75 1;
        flex: 2.75 1;
}

.tab-key,
.backspace,
.enter-key {
    -ms-flex: 2 1;
        flex: 2 1;
}

.double-char-key {
    font-size: 1rem;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}

.tab-key,
.backspace,
.shift-key,
.caps-key,
.ctr-key,
.command-key,
.enter-key,
.fn-key,
.alt-key {
    font-size: 1rem;
}

.button-has-icon {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
}
.button-has-icon svg {
    width: 1.5rem;
    height: 1.5rem;
}
.backspace,
.enter-key {
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: start;
        align-items: flex-start;
}
.backspace,
.enter-key svg {
    padding-top: 10px;
}



